body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flexbox {
  display: flex;
}

.flexbox.column {
  flex-direction: column;
}

.test {
  border: 1px solid green;
  background-color: aqua;
}

.fullheight {
  min-height: 100%;
  height: 100%;
}

.fullwidth {
  min-width: 100%;
}

.mainfont {
  font-family: "Brandon Grotesque Regular";
}

.lightfont {
  font-family: "BrandonGrotW01-Light";
}

.groupBubble {
  outline: none;
  border: none;
  transition: filter 0.3s;
}

.groupBubble:last-of-type {
  margin-bottom: 10px;
}

.groupBubble:hover {
  transition: filter 0.3s;
  filter: brightness(85%);
}

.groupBubble:active {
  filter: brightness(103%);
}

.groupsPage::-webkit-scrollbar {
  width: 5px;
  border-radius: 5px;
}

.groupsPage::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  width: 5px;
}

.groupsPage::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  width: 5px;
}

.noScroll::-webkit-scrollbar {
  width: 0px;
  border-radius: 5px;
}

.noScroll::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  width: 5px;
}

.noScroll::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  width: 5px;
}

.marginLast:last-of-type {
  margin-bottom: 25px;
}
